export default function SpellCard({ spell }) {
  
    const TitleClass = "text-center p-4 ";
    const FillClass = "flex flex-col gap-4 text-s";

    return (
        <li className="bg-slate-700 rounded-2xl list-none w-5x1 p-4">
        <div className="mb-4">
            <h4 className="text-red-500 mb-4">{spell.name}</h4>
            <p className="italic text-sm text-amber-400">
                {spell.level > 0 && `Level ${spell.level} `}
                {spell.school.name}
                {spell.level === 0 && " cantrip"}
            </p>
        </div>
        <div className="grid grid-cols-2 text-sm rounded-lg bg-gray-900">
            <div className={TitleClass}>
                <p className={FillClass}>Casting Time</p>
                {spell.casting_time}
            </div>
            <div className={TitleClass}>
                <p className={FillClass}>Range</p>
                {spell.range}
            </div>
            <div className={TitleClass}>
                <p className={FillClass}>Components</p>
                {spell.components.join(", ")}
            </div>
            <div className={TitleClass}>
                <p className={FillClass}>Duration</p>
                {spell.duration}
            </div>
        </div>
        </li>
    );
}